body {
    background-color: #000;
    color: #fff;
}

/* Profile */
.profile-image {
    width: 100%;
    height: auto;
}

.profile-genres {
    list-style: none;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}

.profile-genres li {
    margin-right: 0.4em;
    display: inline-block;
}

/* Tracks gallery */
.tracks {
    margin-top: 3em;
}

.track-item {
    position: relative;
    cursor: pointer;
}

.track-item img {
    width: 100%;
    height: auto;
    margin: 1em;
}

.track-overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    width: 100%;
    height: 100%;
    margin: 1em;
    padding: 1em;

    background: rgba(0, 0, 0, 0.7);
    color: white;

    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;

    opacity: 0;
    transition: 1s opacity;
    -webkit-transition: 1s opacity;
}

.track-overlay:hover {
    opacity: 1;
    transition: 1s opacity;
    -webkit-transition: 1s opacity;
}
